import React, {useEffect, useLayoutEffect, useState} from "react";
import Modal from "react-modal";
import Item from "./item";
import CartItem from "./cartitem";
import Img from "gatsby-image";


import './cart.css'
import {PrimaryButton} from "./primary-button";
import {Close, CloseWithLabel} from "./icons";
import {Link} from "gatsby";

const CartItems = ({cartItemList, removeFromCart, changeCartItem, closeCart}) => <div className="cart__items">
        <h1 className="cartHeader">
            Корзина
            <CloseWithLabel style={{marginLeft: 'auto'}} color="#87338B" onClick={closeCart}/>
        </h1>
        {cartItemList.map(({item, count, sizeId}) =>
            <CartItem
                item={item}
                count={count}
                sizeId={sizeId}
                removeFromCart={removeFromCart}
                changeCartItem={changeCartItem}
                key={item.id}/>)}
        <div className="cart__actions">
            <div className="cart__total-label">
                Итого:
                <span className="cart__total-number">
                                {cartItemList.map(({item, count}) => item.price * count)
                                    .reduce((a,b) => a+b, 0)}.-
                            </span>
            </div>
            <PrimaryButton className="cart__checkout" to="checkout">Оформить заказ<div className="send"/></PrimaryButton>
        </div>
    </div>

const Cart = ({cartItems, items, removeFromCart, changeCartItem, bannerFixed}) => {
    const [isCartOpen, setIsCartOpen] = useState(false);
    const [cartItemsCount, setCartItemsCount] = useState(null);
    const [shouldAnimate, setShouldAnimate] = useState(false);

    const itemMap = new Map(items.map(item => [item.node.id, item.node]))
    const cartItemList = Object.entries(cartItems)
        .map(([id, {count, sizeId}]) => ({item: itemMap.get(id), count, sizeId}))
        .filter(({item}) => !!item)

    useEffect(() => {
        const oldCount = cartItemsCount;
        const newCount = cartItemList.map(({count}) => count).reduce((a,b) => a + b, 0);
        if(oldCount !== null && oldCount !== newCount) {
            setShouldAnimate(true);
        }
        setCartItemsCount(newCount);
        if(newCount === 0) {
            setIsCartOpen(false)
        }
    }, [cartItems, cartItemsCount, setCartItemsCount, setIsCartOpen]);

    const className = `cartIconOpen${cartItemsCount > 0 ? '' : ' noItems'}${shouldAnimate ? ' cartAnimate' : ''}`;

    useLayoutEffect(() => {
        if(window) {
            window.document.documentElement.dataset.isCartOpen = isCartOpen.valueOf();
        }
    }, [isCartOpen])

    return (
        <>
            <div className={className} onClick={() => setIsCartOpen(!isCartOpen)}>{cartItemsCount}</div>
            {isCartOpen && <PrimaryButton className="cart__checkout__mobile" to="checkout">Оформить заказ<div className="send"/></PrimaryButton>}
            <Modal isOpen={isCartOpen}
                   onRequestClose={() => setIsCartOpen(false)}
                   closeTimeoutMS={200}
                   className="cart-modal__content"
                   overlayClassName="cart-modal__overlay">
                <CartItems
                    cartItemList={cartItemList}
                    removeFromCart={removeFromCart}
                    changeCartItem={changeCartItem}
                    closeCart={() => setIsCartOpen(false)}/>
                <div className="banner">
                    <Img fixed={bannerFixed} style={{marginBottom: 0}}/>
                    <p>
                        <a>Зимние ботинки для девочек</a>
                        {'\n'}
                        <a>Зимние ботинки для мальчиков</a>
                    </p>
                </div>
            </Modal>
        </>
    )
}

export default Cart;
