import {Link} from "gatsby";
import React from "react";
import Img from "gatsby-image"
import './cartitem.css'

export const CheckoutItem = ({item, count, sizeId, removeFromCart}) => (
    <div key={item.id} style={{display: 'flex', alignItems: 'center', marginBottom: 16}}>
        <Img fixed={item.images[0].localFile.childImageSharp.fluid} style={{width: 92, height: 92, borderRadius: 10, marginRight: 20}}/>
        <div style={{flex: 1}}>
            <span className="checkout-item__header">
                {item.name}
            </span>
            <div className="checkout-item__controls-row">
                <span><b>Размер:</b> {item.sizetable.find(({id}) => id === sizeId).size}</span>
            </div>
            <div className="checkout-item__controls-row">
                <span><b>{count} шт.</b></span>
                <span>{item.price}.-</span>
                <div className="checkout-item__total">{item.price * count}.-</div>
            </div>
        </div>
    </div>
)

const CartItem = ({item, count, sizeId, removeFromCart, changeCartItem}) => (
    <div key={item.id} style={{display: 'flex', alignItems: 'center', marginBottom: 16}}>
        <Img fixed={item.images[0].localFile.childImageSharp.fluid} style={{width: 92, height: 92, borderRadius: 10, marginRight: 20}}/>
        <div style={{flex: 1}}>
            <span className="cart-item__header">
                {item.name}
            </span>
            <div className="cart-item__controls-row">
                <span><b>Размер:</b>
                    <select className="select" name="dropdown"
                            onChange={({target}) => changeCartItem({id: item.id, sizeId: +target.value})}>
                    {item.sizetable.map(({id, size}) =>
                        <option value={id} selected={id === sizeId}>{size}</option>)}
                    </select>
                </span>
                <span><select className="select" name="dropdown"
                        onChange={({target}) => changeCartItem({id: item.id, count: +target.value})}>
                    {[...(count > 5 ? [count] : []),1,2,3,4,5,0].map(num =>
                        <option value={num} selected={num === count}>{num} шт.</option>)}
                </select></span>
                <span>{item.price}.-</span>
            </div>
        </div>
        <span className="cart-item__total">{item.price * count}.-</span>
    </div>
)

export default CartItem;
